*{
  margin:0px;
  padding: 0px;
  box-sizing: border-box;
}
.App {
  text-align: center;
  padding: 47.5px;
}
body{
  width: 100%;
  min-height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  font-family: Russo One;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
  animation: pulse 2.7s forwards;
}
@keyframes pulse {
  0% {
  background: radial-gradient(#ffffff, #4e5250);
  }
  100% {
  background-color: rgb(35, 31, 34);
  }
}

.main-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 50%;
  height: auto;
  min-height: 250px;
  padding: 40px;
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 5px;
}
.img1 {
  position: relative;
  width: 180px;
  animation: logo-spin 2s linear;
  
}

@keyframes logo-rotate {
  30% {
    transform: rotate(20deg);
  }
  60% {
    transform: rotate(-20deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes logo-spin {
  from {
    left: -1300px;
    transform: rotate(0deg);
  }
  to {
    left: 0px;
    transform: rotate(1440deg);
  }
}


/*Style pads*/
.main-puds {
  display: grid;
  grid-template-columns: 100px 100px 100px;
  grid-template-rows: 80px 80px 80px; 
  column-gap: 12.5px;
  row-gap: 12.5px;
  padding: 20px;

}
.pad {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin-right: 0px;
  border-radius: 5px;
  
}
.drum-pud1 {
  background: radial-gradient(#ffffff, #f3e308);
  animation: fadeInTopLeft;
  animation-duration: 2.4s;

}
.drum-pud1:active {
  background: radial-gradient(#cfc296, #ffe602);
  outline: none;
  margin-top: 3px;
}


/*Component 2 Css*/
.right-side {
  padding: 50px;
}
.empty-div {
  width: 200px;
  height: 50px;
  border-radius: 10px;
  background: radial-gradient(#ffffff, #e4e4e4);
}
/*type=range design*/
input[type="range"]{
    min-width: 100%;
    padding: 20px;
    border-radius: 5px;
}

input[type=range]{
    -webkit-appearance: none;
}

input[type=range]::-webkit-slider-runnable-track {
    height: .35em;
    background: rgb(218, 221, 207);
    border: none;
    border-radius: 3px;
}

input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: none;
    height: 1.1em;
    width: 1.1em;
    border-radius: 50%;
    background: rgb(122, 117, 145);
    margin-top: -4px;
    cursor: pointer;
}

input[type=range]:focus {
    outline: none;
}

.power-off {
  display: flex;
  justify-content: flex-start;
  margin-left: 60px;
  align-items: center;
  background-color: white;
  width: 50px;
  height: 25px;
  padding: 5px;
  border-radius: 10px;
}
.offVolume {
  background-color: rgb(122, 117, 145);
  width: 15px;
  height: 15px;
  border-radius: 50%;
  cursor: pointer;
  border:none;
}
.offVolume:focus {
  outline: none;

}
.spanWord {
  font-size: 22px;
  color: rgb(208, 200, 245);
}
